* {
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}
img {
  display: block;
  max-width: 100%;
}
#datetime-picker {
  font-size: 18px;
  border-radius: 10px;
  border-style: none;
  background: #fff;
  border-style: groove;
  border-color: #212121;
}
button {
  font-size: 28px;
  border-radius: 10px;
  background: #fff;
  border-style: groove;
  border-color: #212121;
}
.timer {
  display: flex;
}
.field {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 20px;
  font-size: 30px;
}
.value {
  font-size: 40px;
}